.user_container {
  width: 100%;
  height: 150px;
  margin: 1px 0;
  background: #393a5e;
  background: linear-gradient(180deg, #393a5e 0%, #282a3f 100%);
}

.user_container .user_content {
  max-width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.user_container .user_content .user_thumbnail {
  height: 70%;
  border-radius: 50%;
  margin-right: 20px;
}

.user_container .user_content .user_infos {
  margin-right: auto;
}

.user_container .user_content .user_infos p {
  font-size: 1.5rem;
  color: #E2E2E2;
}

.user_container .user_content button {
  padding: 5px 10px;
  border: 1px solid #dcdcdc;
  background: #dcdcdc;
  border-radius: 5px;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .user_container {
    height: 125px;
  }
  .user_container .user_content {
    max-width: 95%;
  }
  .user_container .user_content .user_thumbnail {
    margin-right: 10px;
  }
  .user_container .user_content .user_infos p {
    font-size: 1rem;
  }
}

@media screen and (max-width: 768px) and (max-width: 480px) {
  .user_container .user_content .user_infos p {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 768px) {
  .user_container .user_content button {
    padding: 2px 5px;
  }
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
}

.modal_box {
  width: 80vw;
  height: 40vh;
  margin: auto;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  background-color: #E2E2E2;
  border-radius: 6px;
  position: fixed;
  left: 10vw;
  top: 10vh;
  display: none;
}

.modal_box .modal_title {
  width: 100%;
  height: 80px;
  background: #474971;
  border-radius: 5px 5px 0px 0px;
  padding: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.modal_box .modal_title p {
  font-size: 1.5rem;
  color: #E2E2E2;
}

.modal_box .modal_title p span {
  font-size: 1.5rem;
  color: #c9d73c;
}

@media screen and (max-width: 500px) {
  .modal_box .modal_title p {
    font-size: 1rem;
  }
  .modal_box .modal_title p span {
    font-size: 1rem;
  }
}

.modal_box .modal_form {
  width: 100%;
  height: calc(100% - 80px);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 10px;
}

.modal_box .modal_form button, .modal_box .modal_form input, .modal_box .modal_form select, .modal_box .modal_form textarea {
  margin: 10px 0;
  height: 35px;
  border: 1px solid #bfbfbf;
  border-radius: 10px;
  background: #ffffff;
}

.modal_box .modal_form input {
  width: 90%;
  padding: 0px 5px;
}

.modal_box .modal_form select {
  width: 90%;
}

.modal_box .modal_form .modal_button {
  width: 80px;
  font-weight: bold;
}

.modal_box .modal_content {
  width: 100%;
  height: calc(100% - 100px);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  padding: 10px;
}

.modal_box .modal_content h2 {
  font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
  .modal_box .modal_content h2 {
    font-size: 1rem;
  }
}

.modal_box .modal_content button {
  padding: 5px 10px;
  border: 1px solid #bfbfbf;
  border-radius: 10px;
  font-weight: bold;
}

@media screen and (min-width: 1024px) {
  .modal_box {
    max-width: 50%;
    left: calc(70% - 45%);
  }
}
